@use "@app/bancor-components/Element/Form/InputNumber";
@use "@app/bancor-components/Element/Form/InputSelect";
@use "@app/bancor-components/Element/Form/Input";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/Form";
@use "@app/bancor-components/Element/Image";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Font";

.debitInfo {
    > header {
        & #{Text.$selector} {
            @include Margin.set($bottom: alpha);
            @include Font.set($size: iota, $weight: bold);
            word-break: break-word;
        }
        & #{Image.$selector} {
            @include Image.asModoLogo;
        }
    }

    #{Form.$selector} {
        #{InputSelect.$selector},
        #{Input.$selector},
        #{InputNumber.$selector} {
            @include Margin.set($top: alpha, $bottom: alpha);
        }

        #{Button.$selector} {
            @include Margin.set($bottom: beta);
        }

        .contactInfo {
            @include Margin.set($top: alpha, $bottom: alpha);
            section {
                @include Flex.set($dir: row, $align: center);
                #{Text.$selector} {
                    @include Margin.set($left: alpha);
                }
                width: Layout.get-max-width(alpha);
            }
        }
    }

    &.coupon {
        #{Form.$selector} {
            @include Flex.set($dir: column);

            #{InputNumber.$selector} {
                order: 1;
            }

            .subtitle {
                order: 2;
            }

            .contactInfo {
                order: 3;
            }

            .debitSubtitle {
                order: 4;
            }

            #{InputSelect.$selector} {
                order: 5;
            }

            #{Input.$selector} {
                order: 6;
            }

            #{Button.$selector} {
                order: 7;
            }
        }
    }
}
